import * as React from 'react';
import {
	Switch,
	Route,
	Redirect,
	RouteComponentProps,
	withRouter,
	Link as RouterLink
} from 'react-router-dom';
import Loadable from '@loadable/component';
import clsx from 'clsx';
import '../assets/styles/index.scss';
import {
	Paper,
	withStyles,
	WithStyles,
	withWidth,
	WithWidth,
	isWidthUp,
	isWidthDown
} from '@material-ui/core';
import Collapse from '@material-ui/core/Collapse';
import CloseIcon from '@material-ui/icons/Close';
import { SnackbarProvider } from 'notistack';
import Link from '@material-ui/core/Link';
import { get } from 'lodash';
import { connect } from 'react-redux';
import { Facebook, LinkedIn } from '@material-ui/icons';
import IconButton from '@material-ui/core/IconButton';
import { menuOptions, otherMenuOptions } from '../constants/resources';
import Style from './style';
import { RootState } from '../reducers';
import { ScreenSizeAction } from '../redux/Screen/types';
import {
	ModeTypeEnums,
	UpdateSiteIdAction,
	UpdateViewModeAction
} from '../redux/FeedbackForm/types';

const Home = Loadable(() => import('../pages/Home'));
const About = Loadable(() => import('../pages/About'));
const SocialOutreach = Loadable(() => import('../pages/Social'));
const Contact = Loadable(() => import('../pages/Contact'));
const Categories = Loadable(() => import('../pages/Categories'));
const Sustainability = Loadable(() => import('../pages/Sustainability'));
const TermsAndConditions = Loadable(() => import('../pages/TermsAndConditions'));
const PrivacyPolicy = Loadable(() => import('../pages/PrivacyPolicy'));
const Disclaimer = Loadable(() => import('../pages/Disclaimer'));
const InvestorRelations = Loadable(() => import('../pages/InvestorRelations'));
const Feedback = Loadable(() => import('../pages/Feedback'));
const PartnerWithUs = Loadable(() => import('../pages/PartnerWithUs'));
interface Props extends WithStyles<typeof Style>, RouteComponentProps, WithWidth {
	[name: string]: any;
	desktopViewAction: typeof ScreenSizeAction.action;
	isDesktopView: boolean;
	updateViewMode: typeof UpdateViewModeAction.action;
	updateSiteId: typeof UpdateSiteIdAction.action;
	kioskMode: boolean;
}

interface State {
	open: boolean;
}
/**
 * Application sample.
 * @param {any} props - Properties
 * @returns {React.Component} component.
 */
class App extends React.PureComponent<Props, State> {
	constructor(props: Props) {
		super(props);
		this.state = {
			open: false
		};
	}

	componentDidMount() {
		const { width, desktopViewAction } = this.props;
		if (isWidthUp('sm', width) && !isWidthDown('sm', width)) {
			desktopViewAction(true);
		} else if (isWidthDown('sm', width)) {
			desktopViewAction(false);
		}
		const { location, updateViewMode } = this.props;
		const { search } = location;
		const url = new URLSearchParams(search);
		const mode = url.get('mode') || '';
		const siteId = url.get('key') || '';
		if (siteId !== '') {
			const { updateSiteId } = this.props;
			if (mode === ModeTypeEnums.QR) {
				let decodedKey = '';
				try {
					decodedKey = window.atob(siteId);
				} catch (e) {
					console.log('Wrong key input');
				}
				updateSiteId(decodedKey);
				updateViewMode(decodedKey === '' ? ModeTypeEnums.WEBSITE : mode);
			} else {
				updateSiteId(siteId);
				updateViewMode(mode);
			}
		}
	}

	componentDidUpdate() {
		const { width, desktopViewAction } = this.props;
		if (isWidthUp('sm', width) && !isWidthDown('sm', width)) {
			desktopViewAction(true);
		} else if (isWidthDown('sm', width)) {
			desktopViewAction(false);
		}
	}

	openMenu = (open: boolean) => {
		this.setState({
			open
		});
	};

	getLink = (item) => {
		const { classes } = this.props;
		const externalLink = get(item, 'externalLink', '');
		const disabledLink = get(item, 'disabled', false);

		// Take externally
		if (externalLink !== '') {
			return (
				<Link
					key={item.name}
					className={clsx(classes.hamburgerOption)}
					href={externalLink}
					target="__blank__"
					rel="noopener noreferrer"
					color="inherit"
					component="a"
					onClick={() => this.openMenu(false)}
				>
					{item.name}
				</Link>
			);
		}
		const { history } = this.props;
		const { location } = history;
		const path = get(location, 'pathname', '/');
		return (
			<Link
				key={item.name}
				className={clsx(
					{
						[classes.disabledLink]: disabledLink,
						[classes.active]: path === item.link
					},
					classes.hamburgerOption
				)}
				to={item.link}
				color="inherit"
				component={RouterLink as any}
				onClick={() => this.openMenu(false)}
			>
				{item.name}
			</Link>
		);
	};

	getOtherLink = (item) => {
		const { classes } = this.props;
		const externalLink = get(item, 'externalLink', '');
		const disabledLink = get(item, 'disabled', false);

		if (externalLink !== '') {
			return (
				<Link
					key={item.name}
					className={clsx(classes.hamburgerOtherOption, classes.mediumFont)}
					href={externalLink}
					target="__blank__"
					rel="noopener noreferrer"
					color="inherit"
					component="a"
					onClick={() => this.openMenu(false)}
				>
					{item.name}
				</Link>
			);
		}
		return (
			<Link
				key={item.name}
				className={clsx(classes.hamburgerOtherOption, classes.mediumFont, {
					[classes.disabledLink]: disabledLink
				})}
				to={item.link}
				color="inherit"
				component={RouterLink as any}
				onClick={() => this.openMenu(false)}
			>
				{item.name}
			</Link>
		);
	};

	/**
	 * Render method
	 * @returns {JSX.Element} Jsx.
	 */
	public render() {
		const { classes, isDesktopView, kioskMode } = this.props;
		const { REACT_APP_BASE_URL = '' } = process.env;
		const { open } = this.state;
		return (
			<div className={classes.root}>
				<SnackbarProvider
					maxSnack={1}
					classes={{
						variantSuccess: classes.success,
						variantError: classes.error,
						variantWarning: classes.warning,
						variantInfo: classes.info
					}}
					dense
				>
					<div className={classes.headerContainer}>
						{kioskMode ? (
							<Paper className={classes.header} square>
								<img src="/logo.png" alt="Dmart" className={classes.logo} />
							</Paper>
						) : (
							<>
								<Paper className={classes.header} square>
									<Link
										to="/"
										color="inherit"
										component={RouterLink as any}
										onClick={() => this.openMenu(false)}
									>
										<img src="/logo.png" alt="Dmart" className={classes.logo} />
									</Link>
									<div className={classes.hamburgerOptionsDesktop}>
										{menuOptions
											.filter((item) => {
												const mobileOnly = get(item, 'mobileOnly', false);
												const desktopOnly = get(item, 'desktopOnly', true);
												return !mobileOnly && desktopOnly;
											})
											.map((item) => this.getLink(item))}
									</div>
									<div className={classes.socialMediaOption}>
										<IconButton
											size="small"
											component="a"
											target="__blank__"
											rel="noopener noreferrer"
											href="https://www.facebook.com/Avenue-Supermarts"
										>
											<Facebook className={classes.facebook} />
										</IconButton>
										<IconButton
											size="small"
											edge="end"
											component="a"
											target="__blank__"
											rel="noopener noreferrer"
											href="https://www.linkedin.com/company/avenue-supermarts-pvt-ltd"
										>
											<LinkedIn className={classes.linkedIn} />
										</IconButton>
									</div>
									{open ? (
										<span
											className={classes.hamburger}
											onClick={() => this.openMenu(false)}
											role="presentation"
											aria-label="close-button"
										>
											<CloseIcon color="primary" />
										</span>
									) : (
										<span
											className={classes.hamburger}
											onClick={() => this.openMenu(true)}
											role="presentation"
											aria-label="ham-button"
										>
											<img
												src="/images/icon-menu.png"
												alt="menu-icon"
												className={classes.icon}
											/>
										</span>
									)}
								</Paper>
								<Collapse in={open} className={classes.hamburgerContent}>
									<div className={classes.hamburgerOptions}>
										{menuOptions
											.filter((item) => {
												const mobileOnly = get(item, 'mobileOnly', true);
												const desktopOnly = get(item, 'desktopOnly', false);
												return mobileOnly && !desktopOnly;
											})
											.map((item) => this.getLink(item))}
										<div className={classes.hamburgerOtherOptions}>
											{otherMenuOptions.map((item) =>
												this.getOtherLink(item)
											)}
										</div>
									</div>
								</Collapse>
							</>
						)}
					</div>
					<Switch>
						<Route path="/" exact>
							<Home />
						</Route>
						<Route path="/about-us" exact>
							<About />
						</Route>
						<Route path="/social-outreach" exact>
							<SocialOutreach />
						</Route>
						<Route path="/contact-us" exact>
							<Contact />
						</Route>
						<Route path="/sustainability" exact>
							<Sustainability />
						</Route>
						<Route path="/categories" exact>
							<Categories />
						</Route>
						<Route path="/investor-relationship" exact>
							<InvestorRelations />
						</Route>
						<Route path="/feedback" exact>
							<Feedback />
						</Route>
						<Route path="/partner-with-us" exact>
							<PartnerWithUs />
						</Route>
						<Route path="/terms-and-conditions" exact>
							<TermsAndConditions />
						</Route>
						<Route path="/privacy-policy" exact>
							<PrivacyPolicy />
						</Route>
						<Route path="/disclaimer" exact>
							<Disclaimer />
						</Route>
						<Route path="*">
							<Redirect to={REACT_APP_BASE_URL} />
						</Route>
					</Switch>
				</SnackbarProvider>
			</div>
		);
	}
}

const mapStateToProps = (state: RootState) => ({
	isDesktopView: state.screenReducer.isDesktopView,
	kioskMode: state.feedbackReducer.kioskMode
});

const mapDispatchToProps = {
	desktopViewAction: ScreenSizeAction.action,
	updateViewMode: UpdateViewModeAction.action,
	updateSiteId: UpdateSiteIdAction.action
};

export default withStyles(Style, { name: 'DmartCorpFresh' })(
	withWidth()(withRouter(connect(mapStateToProps, mapDispatchToProps)(App)))
);
